import { combineReducers } from 'redux';
import {
  ATTEMPT_LOGIN,
  LOGIN_ATTEMPT,
  CHANGE_APP_PAGE,
  ATTEMPT_LOGOUT,
  CONTROL_DIALOG,
} from './actions';

const user = (state = { authed: false }, action) => {
  switch (action.type) {
    case ATTEMPT_LOGIN:
      return {
        ...state,
      };
    case LOGIN_ATTEMPT:
      return {
        ...state,
        authed: true,
        ...action.user,
        token: action.token,
        theme: 'colorless',
      };
    case ATTEMPT_LOGOUT:
      return {
        authed: false,
      };
    default:
      return state;
  }
};

const page = (state = -1, action) => {
  switch (action.type) {
    case CHANGE_APP_PAGE:
      return action.page;
    default:
      return state;
  }
};

const dialog = (state = -1, action) => {
  switch (action.type) {
    case CONTROL_DIALOG:
      return action.dialog;
    default:
      return state;
  }
};


const rootReducer = combineReducers({ user, page, dialog });

export default rootReducer;



