import React from 'react';
import PropTypes from 'prop-types';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';

const colors = {
  white: {
    light: '#ffffff',
    main: '#fffbd5',
    dark: '#ccc8a4',
    contrastText: '#000',
  },
  blue: {
    light: '#ddffff',
    main: '#aae0fa',
    dark: '#79aec7',
    contrastText: '#000',
  },
  black: {
    light: '#fef5f2',
    main: '##cbc2bf',
    dark: '#9a928f',
    contrastText: '#000',
  },
  red: {
    light: '#ffdcbf',
    main: '#f9aa8f',
    dark: '#c47a61',
    contrastText: '#000',
  },
  green: {
    light: '#cdffe0',
    main: '#9bd3ae',
    dark: '#6ba27f',
    contrastText: '#000',
  },
  colorless: {
    light: '#fff5f3',
    main: '#ccc2c0',
    dark: '#9b9290',
    contrastText: '#000',
  },
};

const createTheme = secondary => createMuiTheme({
  palette: {
    primary: {
      light: '#49443b',
      main: '#211d15',
      dark: '#000000',
      contrastText: '#fff',
    },
    secondary,
  },
});
const UserTheme = props => (
  <MuiThemeProvider theme={createTheme(colors[props.theme])}>
    {props.children}
  </MuiThemeProvider>
);


export default UserTheme;



