import React, { Component } from 'react';
import PropTypes from 'prop-types';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import AssistantIcon from '@material-ui/icons/Assistant';
import DashboardIcon from '@material-ui/icons/Dashboard';
import Button from '@material-ui/core/Button';
import LibraryBooksIcon from '@material-ui/icons/LibraryBooks';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';

import '../App.css';

import Login from '../container/Login';
import MainAppBar from '../container/MainAppBar';
import FullscreenDialog from '../container/FullscreenDialog';
import UserTheme from '../container/UserTheme';

const styles = {
  root: {
    width: '100%',
    position: 'absolute',
    bottom: 0,
  },
  rootContainer: {
    flexGrow: 1,
  },
};

class App extends Component {
  constructor(props) {
    super(props);
    this.props.initApp();
    this.getCurrentView = this.getCurrentView.bind(this);
    this.changePage = this.changePage.bind(this);
  }

  getCurrentView() {
    switch (this.props.page) {
      case 0:
        return <div>Library</div>;
      case 1:
        return (
          <div>
            <Button variant="raised" color="secondary" >
              Secondary
            </Button>
          </div>);
      case 2:
        return <div>Wizard</div>;
      default:
        return <Login />;
    }
  }

  changePage(_, value) {
    if (this.props.authed) {
      this.props.changeAppPage(value);
    }
  }

  render() {
    const { classes } = this.props;

    return (
      <div className={classes.rootContainer}>
        <UserTheme>
          <FullscreenDialog />
          <MainAppBar />
          <Grid container>
            <Grid item xs={12} sm={12} md={4} />
            <Grid item xs={12} sm={12}md={4}>
              {this.getCurrentView()}
            </Grid>
            <Grid item xs={12} sm={12} md={4} />

            <BottomNavigation
              value={this.props.page}
              onChange={this.changePage}
              showLabels
              className={classes.root}
            >
              <BottomNavigationAction label="Library" icon={<LibraryBooksIcon />} />
              <BottomNavigationAction label="Dashboard" icon={<DashboardIcon />} />
              <BottomNavigationAction label="Wizard" icon={<AssistantIcon />} />
            </BottomNavigation>
          </Grid>
        </UserTheme>
      </div>
    );
  }
}

App.propTypes = {
  page: PropTypes.number.isRequired,
  classes: PropTypes.object.isRequired,
  changeAppPage: PropTypes.func.isRequired,
};

export default withStyles(styles)(App);



