const ATTEMPT_LOGIN = 'ATTEMPT_LOGIN';
const LOGIN_ATTEMPT = 'LOGIN_ATTEMPT';
const CHANGE_APP_PAGE = 'CHANGE_APP_PAGE';
const ATTEMPT_LOGOUT = 'ATTEMPT_LOGOUT';
const CONTROL_DIALOG = 'CONTROL_DIALOG';

const attemptLogin = () => ({ type: ATTEMPT_LOGIN });
const receiveLoginAttempt = (user, token) => ({ type: LOGIN_ATTEMPT, user, token });
const setCredentials = (user, token) => ({ type: LOGIN_ATTEMPT, user, token });
const changeAppPage = page => ({ type: CHANGE_APP_PAGE, page });
const attemptLogout = () => ({ type: ATTEMPT_LOGOUT });
const controlDialog = dialog => ({ type: CONTROL_DIALOG, dialog });

const login = (username, password) => (dispatch) => {
  dispatch(attemptLogin());

  return fetch('/login', {
    method: 'post',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      Authorization: `Basic ${btoa(`${username}:${password}`)}`,
    },
  })
    .then(response => response.json())
    .then((response) => {
      if (response.token) {
        localStorage.setItem('credentials', JSON.stringify(response));
        dispatch(receiveLoginAttempt(response.user, response.token));
        dispatch(changeAppPage(1));
      }
    });
};

const logout = () => (dispatch) => {
  dispatch(attemptLogout());
  dispatch(changeAppPage(-1));
  dispatch(controlDialog(-1));
  localStorage.clear();
};

module.exports = {
  ATTEMPT_LOGIN,
  LOGIN_ATTEMPT,
  CHANGE_APP_PAGE,
  ATTEMPT_LOGOUT,
  CONTROL_DIALOG,
  controlDialog,
  attemptLogin,
  receiveLoginAttempt,
  changeAppPage,
  setCredentials,
  attemptLogout,
  login,
  logout,
};



